* {
  padding: 0;
  margin: 0;
}

.cover {
  background-color: #364299;
  overflow: hidden;
}
.wrapper {
  padding: 15% 30%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 800px;
  height: 550px;
  border: 2px solid #1CE;
}

.image {
  width: 100%;
}

.button {
  outline: none;
  border: 3px solid #6e79ce;
  border-radius: 50px;
  margin: 10px;
  padding: 5px;
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.button:hover {
  background-color: grey;
  cursor: pointer;
  border: 3px solid #90919e;
  color: aliceblue;
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.button span {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  font-size: 14px;
}



